.sub-container{
    width: 100%;
}


.sub-container *{
    color: #3E3F44;
    line-height: 180%;
}

.conclusion-container{
    padding-bottom: 300px !important;
}

.sub-title{
    padding: 100px 0;
    margin: 0 auto;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
}

.sub-title-left-panel{
    width: 50%;
    font-size: 36px;
    font-weight: bold;
}

.sub-title-left-panel>p{
    margin: 0;
    line-height: 160%;
    color: #1F1F22 !important;
}

.sub-title-right-panel{
    width: 50%;
    font-size: 32px;
    font-weight: normal;
}

.sub-title-right-panel>p{
    margin: 0;
    line-height: 150%;
    color: #1F1F22 !important;
}

.sub-title-image{
    padding-bottom: 100px;
}

.sub-title-image>img{
    width: 100%;
}

.sub-component{
    width: 80%;
    margin: 0 auto;
    max-width: 1400px;
}

.sub-project-properties{
    display: grid;
    grid-template: auto / auto auto;
    grid-row-gap: 50px;
    padding-bottom: 200px;
}

.sub-project-properties-item{
    text-align: left;
}

.properties-title{
    color: #989AA4;
    font-weight: 500;
    font-size: 14px;
    line-height: 160%;
    padding-bottom: 20px;
}

.properties-value{
    font-weight: 500;
    font-size: 18px;
    line-height: 160%;
    color: #3E3F44;
}

.properties-value>p{
    margin: 0;
}

.sub-section{
    padding: 200px 0;
    text-align: left;
}

.sub-section>p{
    font-weight: 600;
    font-size: 36px;
    line-height: 160%;
    color: #6B89AD;
    margin-bottom: 40px;
}

.sub-section-detail{
    display: grid;
    flex-direction: row;
    grid-row-gap: 145px;
}

.sub-section-detail-half-column{
    display: grid;
    grid-template: auto / 50% 50%;
    flex-direction: row;
}

.sub-section-detail-column-string{
    padding-bottom: 40px;
}

.sub-section-detail-image{
    display: inline-block;
    float: left;
    height: 20px;
    padding-right: 5px;
}

.sub-section-detail-string{
    display:flow-root;
}

.left-padding-100{
    padding-left: 100px;
}

.sub-section-detail-left{
    font-size: 28px;
    font-weight: 600;
}

.sub-section-detail-right{
    font-size: 12pt;
    font-weight: 400;
}

.sub-section-detail-title{
    font-size: 22pt;
    font-weight: 600;
    padding-bottom: 30px;
}

.sub-section-detail-subtitle{
    padding-bottom: 60px;
}

.sub-section-grid-title{
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 50px;
}

.sub-section-grid-2{
    display: grid;
    grid-template: auto / auto auto ;
    grid-row-gap: 50px;
    grid-column-gap: 26px;
}

.sub-section-grid-3{
    display: grid;
    grid-template: auto / auto auto auto;
    grid-row-gap: 50px;
    grid-column-gap: 26px;
}

.sub-section-grid-4{
    display: grid;
    grid-template: auto / auto auto auto auto;
    grid-row-gap: 50px;
    grid-column-gap: 26px;
}


.sub-section-grid-item>img {
    width: 100%;
    display: block;
}

.sub-section-center{
    width: 80%;
    margin: 0 auto;
}

.sub-section-center-title{
    font-weight: 700;
    font-size: 18px;
    padding-bottom: 20px;
}

.sub-section-center-contents{
    font-size: 14px;
}
.conclusion-title{
    font-size: 24px !important;
}

.sub-section-bisymetry-contents{
    display: grid;
    flex-direction: row;
    grid-row-gap: 145px;
}

.sub-section-bisymetry-content{
    display: grid;
    grid-template-columns: 40% 40% ;
    grid-column-gap: 20%;
}

.bisymetry-sm-lg-left{
    grid-template-columns: 60% 30% ;
    grid-column-gap: 10%;
}

.bisymetry-sm-lg-right{
    grid-template-columns: 30% 60% ;
    grid-column-gap: 10%;
}

.sub-section-bisymetry-title{
    font-weight: 700;
    font-size: 18px;
    padding-bottom: 20px;
}

.sub-section-bisymetry-content video, .sub-section-bisymetry-content img{
    width: 100%;
}

.sub-section-vertical-center{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}

.sub-section-detail-images-container{
    display: flex;
    grid-template-columns: 30% 30% 30% ;
}

.sub-section-detail-images-item>img{
    width: 30%;
    display: block;
    margin: 0 auto;
    padding-bottom: 30px;
}

.sub-section-detail-images-item>div{
    margin: 0 auto;
    width: 70%;
    font-weight: 600;
    text-align: center;
}

.padding-top-50{
    padding-top: 50px;
}

.font-24{
    font-size: 24px;
}

.weight-600{
    font-weight: 600;
}

.color-dark-blue>div
{
    color: #464E73;
}

.color-bright-green>div, .color-bright-green>p
{
    color: #0FAE91;
}

.color-brighter-green>div, .color-brighter-green>p
{
    color: #0ED2BB;
}

.color-blue>div, .color-blue>p
{
    color: #0013BA;
}

.scale-up{
    transform: scale(1.3);
    padding: 150px 0px;
}

@media only screen and (min-width: 2001px) {
    .scale-up{
        transform: scale(1.3);
        padding: 150px 0px;
    }
}

@media only screen and (max-width: 2000px) {
    .scale-up{
        transform: scale(1.1);
        padding: 50px 0px;
    }
}

